.recharts-tooltip-wrapper{
  pointer-events:auto !important;
}
.pool-chart-box {
  display: flex;
  margin: 0 auto;
  justify-content: space-between;
  display: flex;
  width: 1140px;
  max-width: 1440px;
  border: 1px solid #203B60;
  border-top: 0;
  .chart-filter{
    margin-bottom: 52px;
  }
  .token-list {
    position: absolute;
    display: flex;
    width: 100%;
    height: 118px;
    flex-wrap: wrap;
    padding-left: 4px;
    padding-right: 24px;
    margin-top: 6px;
    li {
      list-style: none;
      font-size: 20px;
      display: flex;
      width: 140px;
      justify-content: space-between;
      margin-top: 8px;
      margin-left: 20px;
      color: #fff;
      .token-name-text{
        color: #D4D3DC;
      }
      .WBNB,
      .BNB,
      .WBTC,
      .WMATIC,
      .amUSDC,
      .BUSD,
      .USDT,
      .DERI,
      .TBUSD,
      .SXP,
      .ETH,
      .LTC,
      .XRP,
      .BTCB,
      .ADA,
      .CAKE,
      .MATIC,
      .AAVE,
      .TUSD,
      .DAI,
      .DOT,
      .LINK,
      .XVS,
      .USDC,
      .TRX,
      .TDERI,
      .UST,
      .DOGE,
      .LUNA {
        width: 20px;
        height: 20px;
        margin-right: 8px;
        margin-left: 8px;
        background-size: 20px 20px;
        background-repeat: no-repeat;
        background-position: center;
      }

      .LTC {
        background-image: url('../../../assets/img/LTC.svg');
      }

      .DOGE {
        background-image: url('../../../assets/img/DOGE.svg');
      }

      .UST {
        background-image: url('../../../assets/img/UST.svg');
      }

      .LUNA {
        background-image: url('../../../assets/img/LUNA.svg');
      }

      .XRP {
        background-image: url('../../../assets/img/XRP.svg');
      }

      .XVS {
        background-image: url('../../../assets/img/XRP.svg');
      }

      .DAI {
        background-image: url('../../../assets/img/DAI.svg');
      }

      .DOT {
        background-image: url('../../../assets/img/DOT.svg');
      }

      .LINK {
        background-image: url('../../../assets/img/LINK.svg');
      }


      .ADA {
        background-image: url('../../../assets/img/ADA.svg');
      }

      .AAVE {
        background-image: url('../../../assets/img/AAVE.svg');
      }

      .TUSD {
        background-image: url('../../../assets/img/TUSD.svg');
      }

      .TRX {
        background-image: url('../../../assets/img/TRX.svg');
      }

      .SXP {
        background-image: url('../../../assets/img/SXP.svg');
      }

      .USDC {
        background-image: url('../../../assets/img/USDC.svg');
      }

      .DERI,
      .TDERI {
        background-image: url('../../../assets/img/logo.svg');
      }

      .ETH {
        background-image: url('../../../assets/img/ETHUSD.svg');
      }

      .BTCB,
      .WBTC {
        background-image: url('../../../assets/img/BTCUSD.svg');
      }

      .WBNB,
      .BNB {
        background-image: url('../../../assets/img/BNB.svg');
      }

      .WMATIC,
      .MATIC {
        background-image: url('../../../assets/img/MATIC.svg');
      }

      .amUSDC {
        background-image: url('../../../assets/img/amusdc.svg');
      }

      .BUSD,
      .TBUSD {
        background-image: url('../../../assets/img/BUSD.svg');
      }

      .USDT {
        background-image: url('../../../assets/img/USDT.svg');
      }

      .CAKE {
        background-image: url('../../../assets/img/CAKE.svg');
      }

      span {
        display: flex;

        img {
          margin: 0 8px;
        }
      }

      .token-name {
        width: 95px;
      }
    }
  }

  .recharts-tooltip-wrapper {
    visibility: visible !important;
  }

  .lp-graph,.token-graph {
    height: 480px;
    display: flex;
    align-items: flex-end;
    border-right: 1px solid #203B60;
  }
  .lp-graph{
    width: 759px;
  }
  .token-graph{
    width: 379px;
    .g-button {
      width: 64px;
    }
  }

  .netValue,
  .tokens {
    display: flex;
    flex-direction: column;
    // justify-content: flex-end;
    padding-top: 28px;
  }

}

@media only screen and (min-width : 1140px) {
  .pool-chart-box {
    width: 1140px;
  }
}

@media only screen and (min-width : 760px) and (max-width:1140px) {
  .pool-chart-box {
    width: 760px;

    .lp-graph,.token-graph {
      width: 379px;

      .g-button {
        width: 64px;
      }
    }
  }
}

@media only screen and (max-width:759px) {
  .pool-chart-box {
    width: 100%;
    min-width: 375px;
    justify-content: space-between;
    display: flex;
    flex-direction: column;
    border:0;
    .lp-graph,.token-graph {
      width: 100%;
      border:0;
    }
  }
}