.guidance-modal {
  width: 640px;
  height: 489px;
  background: #0A172B;
  box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.5);
  border-radius: 4px;

  .title-close {
    display: flex;
    justify-content: space-between;
    margin-top: 31px;
    padding-right: 31px;

    .title-text {
      text-align: center;
      flex: 1;
      font-weight: 600;
      font-size: 22px;
      color: #E0ECFF;
    }

    .close {
      cursor: pointer;
      display: flex;
      align-items: center;
    }
  }

  .step-box {
    width: 459px;
    height: 30px;
    margin: 0 auto;
    margin-top: 29px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .remove,
    .approve,
    .add {
      width: 103px;
      height: 30px;
      background: rgba(28, 45, 75, 0.5);
      border: 1.5px solid rgba(28, 45, 75, 0.8);
      border-radius: 1000px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 14px;
      font-weight: 700;
    }

    .add {
      width: 83px;
    }

    .hr {
      width: 66px;
      height: 6px;
      background: #112140;
      border-radius: 10000px;
    }

    .hr-success {
      background: #59AE99;
    }

    .spinner,
    img {
      margin-right: 8px;
    }

    .pending {
      color: rgba(89, 174, 153, 0.6);
      background: rgba(89, 174, 153, 0.2);
      border: 1.5px solid rgba(89, 174, 153, 0.2);
    }

    .success {
      background: #59AE99;
      color: #E0ECFF;
      border: 1.5px solid rgba(255, 255, 255, 0.5);
    }
  }

  .guidance-info {
    width: 459px;
    height: 129px;
    margin: 0 auto;
    margin-top: 11px;
    color: #93A1B8;

    .now-step {
      font-size: 12px;
      height: 40px;
      line-height: 40px;
      margin-bottom: 9px;
    }

    .pool-type,
    .balance-box {
      font-size: 14px;
      height: 40px;
      line-height: 40px;
      display: flex;
      font-weight: 600;
      justify-content: space-between;

      .pool-type-text,
      .balance-num {
        color: #E0ECFF;
        font-weight: 500;
        display: flex;
        align-items: center;
      }
    }
  }

  .operate-box {
    width: 331px;
    margin: 0 auto;
    margin-top: 39px;

    .input-box {
      margin-bottom: 39px;
    }
  }
}

@media only screen and (max-width: 760px) {
  .guidance-modal {
    border-radius: 20px;
    border: 2px solid #203B60;
    width: 375px;
    height: 527px;

    .step-box {
      margin-top: 58px;
      width: 326px;

      .remove,
      .approve,
      .add {
        font-size: 10px;
      }

      .remove {
        width: 75px;
      }

      .approve {
        width: 80px;
      }

      .add {
        width: 65px;
      }
      .hr{
        width: 40px;
        height: 4px;
      }
    }

    .guidance-info {
      width: 321px;
      margin-top: 43px;
      .now-step{
        margin-bottom: 0px;
      }
    }
    .operate-box{
      margin-top: 36px;
    }
  }
}